import { createHooks } from "@wordpress/hooks";
if (!window.stlaHooks) {
  let stlaHooks = createHooks();
  window.stlaHooks = stlaHooks;
}
export const {
  filters,
  addFilter,
  applyFilters,
  removeFilter,
  removeAllFilters,
  actions,
  addAction,
  doAction,
  removeAction,
  removeAllActions,
} = stlaHooks;

export default stlaHooks;
