import { applyFilters } from "../../../Helpers/createHooks";

const Sidebar = (props) => {
  const sidebarFields = () => {
    let sidebarFields = "";
    sidebarFields = window.wp.hooks.applyFilters(
      "stla_booster_checkbox_radio_sidebar",
      sidebarFields,
      props.checkboxRadioSettings,
      props.setCheckboxRadioSettings,
      props.formId,
      props.setHasChangesMade
    );

    // console.log(props.checkboxRadioSettings);
    return sidebarFields;
  };

  return <div className="stla-panel-sidebar">{sidebarFields()}</div>;
};

export default Sidebar;
