import { useState, useEffect } from "@wordpress/element";
import { isEmpty, merge } from "lodash";

import ContentArea from "./ContentArea";
import Sidebar from "./Sidebar";

const Styler = (props) => {
  // initial load styler settings are empty
  if (isEmpty(props.stylerSettings)) {
    return "Loading styler";
  }

  return (
    <div className="stla-panel">
      <Sidebar
        activeSectionId={props.activeSectionId}
        setActiveSectionId={props.setActiveSectionId}
        stylerSettings={props.stylerSettings}
        setStylerSettings={props.setStylerSettings}
        generalSettings={props.generalSettings}
        setGeneralSettings={props.setGeneralSettings}
        formId={props.formId}
        fetchStylerSettings={props.fetchStylerSettings}
        hasChangesMade={props.hasChangesMade}
        setHasChangesMade={props.setHasChangesMade}
      />

      <ContentArea
        activeSectionId={props.activeSectionId}
        setActiveSectionId={props.setActiveSectionId}
        stylerSettings={props.stylerSettings}
        setStylerSettings={props.setStylerSettings}
        formId={props.formId}
        totalPageCount={props.totalPageCount}
        activePage={props.activePage}
        setActivePage={props.setActivePage}
        allSettings={props.allSettings}
      />
    </div>
  );
};
export default Styler;
