import Sidebar from "./Sidebar";
import ContentArea from "./ContentArea";
import { useState } from "@wordpress/element";
const FieldIcons = (props) => {
  const [activeFieldId, setActiveFieldId] = useState(false);
  return (
    <div className="stla-panel">
      <Sidebar
        fieldIcons={props.fieldIcons}
        setFieldIcons={props.setFieldIcons}
        fieldIconsSettings={props.fieldIconsSettings}
        setFieldIconsSettings={props.setFieldIconsSettings}
        formId={props.formId}
        activeFieldId={activeFieldId}
        setActiveFieldId={setActiveFieldId}
        formFieldsLabels={props.formFieldsLabels}
        hasChangesMade={props.hasChangesMade}
        setHasChangesMade={props.setHasChangesMade}
      />
      <ContentArea
        fieldIcons={props.fieldIcons}
        fieldIconsSettings={props.fieldIconsSettings}
        formId={props.formId}
        activeMenuBar={props.activeMenuBar}
        activeFieldId={activeFieldId}
        setActiveFieldId={setActiveFieldId}
        totalPageCount={props.totalPageCount}
        activePage={props.activePage}
        setActivePage={props.setActivePage}
        hasChangesMade={props.hasChangesMade}
        setHasChangesMade={props.setHasChangesMade}
        allSettings={props.allSettings}
      />
    </div>
  );
};

export default FieldIcons;
