import {
  Button,
  Modal,
  Dashicon,
  FlexItem,
  Flex,
  NoticeList,
} from "@wordpress/components";
import { useState } from "@wordpress/element";

const ProPopup = (props) => {
  const [isModalOpen, setIsModalOpen] = useState(true);

  const showModal = () => {
    const popupBtnStrucutre = () => {
      let buttonOne,
        buttonTwo = "";

      if ("link" in props.content) {
        buttonOne = (
          <FlexItem>
            <Button variant="primary" href={props.content.link}>
              {props.content.primaryText}
            </Button>
          </FlexItem>
        );
      }

      if ("secondaryLink" in props.content) {
        buttonTwo = (
          <FlexItem>
            <Button variant="secondary" href={props.content.secondaryLink}>
              {props.content.SecondaryText}
            </Button>
          </FlexItem>
        );
      }

      return (
        <>
          <Flex justify={"center"}>
            {buttonOne}
            {buttonTwo}
          </Flex>
        </>
      );
    };

    let notice = () => {
      if ("noticeLink" in props.content) {
        return (
          <div className="stla-modal-notice">
            <a href={props.content.noticeLink} target="_blank" rel="noopener">
              {props.content.notice}
            </a>
          </div>
        );
      }
      return <div className="stla-modal-notice">{props.content.notice}</div>;
    };

    return (
      <Modal
        title="Tooltips is not active"
        onRequestClose={() => setIsModalOpen(false)}
        className="stla-pro-modal-container"
        __experimentalHideHeader={true}
        style={{ width: "600px" }}
        isDismissible={false}
        shouldCloseOnClickOutside={false}
        shouldCloseOnEsc={false}
        overlayClassName="stla-modal-overlay"
      >
        <div className="stla-pro-modal">
          {/* <div className="stla-close-modal">
            <Dashicon icon="no-alt" onClick={() => setIsModalOpen(false)} />
          </div> */}

          <div className="stla-modal-icon">
            <Dashicon icon="lock" />
          </div>

          <div className="stla-modal-content">
            <h3>{props.content.title}</h3>
            <p>{props.content.description}</p>
            {popupBtnStrucutre()}
          </div>
          <div className="stla-modal-footer">{notice()}</div>
        </div>
      </Modal>
    );
  };
  return <>{isModalOpen && showModal()}</>;
};

export default ProPopup;
