import Components from "../../../../Helpers/Components";

const FormWrapper = (props) => {
  const componentData = [
    {
      type: "responsive",
      id: ["max-width", "max-width-tab", "max-width-phone"],
      label: "Width",
    },
    { type: "fontFamily", id: "font", label: "Font Family" },
    {
      type: "background",
      id: [
        "background-type",
        "background-color",
        "background-image",
        "gradient-color",
        "background-opacity",
      ],
      label: "Background Type",
    },

    {
      type: "border",
      id: ["border-size", "border-type", "border-color"],
      label: "Border",
    },
    { type: "unit", id: "border-radius", label: "Border Radius" },
    { type: "spacing", id: "margin", label: "Margin" },
    { type: "spacing", id: "padding", label: "Padding" },
  ];

  return (
    <div className="stla-sidebar-settings">
      <Components
        panelData={props.stylerSettings}
        setPanelData={props.setStylerSettings}
        sectionId="form-wrapper"
        settings={componentData}
        hasChangesMade={props.hasChangesMade}
        setHasChangesMade={props.setHasChangesMade}
      />
    </div>
  );
};

export default FormWrapper;
