import { __experimentalUnitControl as UnitControl } from "@wordpress/components";
import { cloneDeep } from "lodash";

const Unit = (props) => {
  const sectionSettings = props.panelData[props.sectionId];
  const updateUnit = (value) => {
    let allStylerSettings = cloneDeep(props.panelData);
    allStylerSettings[props.sectionId][props.settingId] = value;
    props.setPanelData(allStylerSettings);
    props.setHasChangesMade(true);
  };

  return (
    <UnitControl
      label={props.label}
      onChange={updateUnit}
      value={sectionSettings[props.settingId]}
    />
  );
};
export default Unit;
