import { Button, ButtonGroup, Dashicon } from "@wordpress/components";
import { cloneDeep } from "lodash";
const Alignment = (props) => {
  const sectionSettings = props.panelData[props.sectionId];

  const updateAlignment = (value) => {
    let allStylerSettings = cloneDeep(props.panelData);
    allStylerSettings[props.sectionId][props.settingId] = value;
    props.setHasChangesMade(true);
    props.setPanelData(allStylerSettings);
  };

  const alignmentStructure = () => {
    let variantSecondary = "secondary";
    let variantPrimary = "primary";
    let alignmentValue = sectionSettings[props.settingId];

    return (
      <>
        <label className="stla-sidebar-custom-label">{props.label}</label>
        <div>
          <ButtonGroup>
            <Button
              variant={
                alignmentValue === "left" ? variantPrimary : variantSecondary
              }
              onClick={() => updateAlignment("left")}
            >
              <Dashicon icon={"editor-alignleft"} />
            </Button>
            <Button
              variant={
                alignmentValue === "center" ? variantPrimary : variantSecondary
              }
              onClick={() => updateAlignment("center")}
            >
              <Dashicon icon={"editor-aligncenter"} />
            </Button>
            <Button
              variant={
                alignmentValue === "right" ? variantPrimary : variantSecondary
              }
              onClick={() => updateAlignment("right")}
            >
              <Dashicon icon={"editor-alignright"} />
            </Button>
            <Button
              variant={
                alignmentValue === "justify" ? variantPrimary : variantSecondary
              }
              onClick={() => updateAlignment("justify")}
            >
              <Dashicon icon={"editor-justify"} />
            </Button>
          </ButtonGroup>
        </div>
      </>
    );
  };
  return <>{alignmentStructure()}</>;
};
export default Alignment;
