import { MenuItem, Notice } from "@wordpress/components";
import { useState } from "@wordpress/element";
import FormWrapper from "./FormWrapper";
import FormHeader from "./FormHeader";
import FieldLabels from "./FieldLabels";
import SubLabels from "./SubLabels";
import Placeholders from "./Placeholders";
import FieldDescription from "./FieldDescriptions";
import TextFields from "./TextFields";
import DropdownFields from "./DropdownFields";
import RadioInputs from "./RadioInputs";
import CheckboxInputs from "./CheckboxInputs";
import ParagraphTextarea from "./ParagraphTextarea";
import SectionBreakTitle from "./SectionBreakTitle";
import SectionBreakDescription from "./SectionBreakDescription";
import SidebarHeader from "../../../Helpers/sidebarHeader";
import ListField from "./ListField";
import SubmitButton from "./SubmitButton";
import ConfirmationMessage from "./ConfirmationMessage";
import ErrorMessage from "./ErrorMessage";
import FormTitleDescription from "./FormTitleDescription";
import GeneralSettings from "./GeneralSettings";

const Sidebar = (props) => {

  const sections = [
    { label: "General Settings", id: "generalSettings" },
    { label: "Form Wrapper", id: "formWrapper" },
    { label: "Form Header ", id: "formHeader" },
    { label: "Form Title & Description", id: "formTitleDescription" },
    { label: "Field Labels", id: "fieldLabels" },
    { label: "Sub Labels", id: "subLabels" },
    { label: "Placeholders", id: "placeholders" },
    { label: "Field Description", id: "fieldDescriptions" },
    { label: "Text Fields", id: "textFields" },
    { label: "Dropdown Fields", id: "dropdownFields" },
    { label: "Radio Inputs", id: "radioInputs" },
    { label: "Checkbox Inputs", id: "checkboxInputs" },
    { label: "Paragraph Textarea Fields", id: "paragraphTextareaFields" },
    {
      label: "Section Break Title & Description",
      id: "sectionBreakTitleDescription",
    },
    { label: "List Field", id: "listField" },
    { label: "Submit Button", id: "submitButton" },
    { label: "Confirmation Message", id: "confirmationMessage" },
    { label: "Error Message", id: "errorMessage" },
  ];

  const sectionsHTML = () => {
    let panelStructure = (
      <Notice status="warning" isDismissible={false}>
        Please choose a form in order to apply the styles.
      </Notice>
    );

    if (!!props.formId) {
      panelStructure = sections.map((section) => {
        return (
          <MenuItem
            className="stla-sidebar-panel"
            onClick={() => props.setActiveSectionId(section.id)}
            icon="arrow-right-alt2"
          >
            {section.label}
          </MenuItem>
        );
      });
    }

    return panelStructure;
  };

  return (
    <div className="stla-panel-sidebar">
      
      {props.activeSectionId === "" && sectionsHTML()}

      {props.activeSectionId !== "" && (
        <SidebarHeader
          activeSectionId={props.activeSectionId}
          setActiveSectionId={props.setActiveSectionId}
          sections={sections}
        />
      )}

      {/* Form Wrapper */}
      {props.activeSectionId === "formWrapper" && (
        <FormWrapper
          stylerSettings={props.stylerSettings}
          setStylerSettings={props.setStylerSettings}
          hasChangesMade={props.hasChangesMade}
          setHasChangesMade={props.setHasChangesMade}
        />
      )}

      {/* Form Header */}
      {props.activeSectionId === "formHeader" && (
        <FormHeader
          stylerSettings={props.stylerSettings}
          setStylerSettings={props.setStylerSettings}
          hasChangesMade={props.hasChangesMade}
          setHasChangesMade={props.setHasChangesMade}
        />
      )}

      {/* Form Header */}
      {props.activeSectionId === "formTitleDescription" && (
        <FormTitleDescription
          stylerSettings={props.stylerSettings}
          setStylerSettings={props.setStylerSettings}
          hasChangesMade={props.hasChangesMade}
          setHasChangesMade={props.setHasChangesMade}
        />
      )}

      {/* Field Labels */}
      {props.activeSectionId === "fieldLabels" && (
        <FieldLabels
          stylerSettings={props.stylerSettings}
          setStylerSettings={props.setStylerSettings}
          hasChangesMade={props.hasChangesMade}
          setHasChangesMade={props.setHasChangesMade}
        />
      )}

      {/* Sub Labels */}
      {props.activeSectionId === "subLabels" && (
        <SubLabels
          stylerSettings={props.stylerSettings}
          setStylerSettings={props.setStylerSettings}
          hasChangesMade={props.hasChangesMade}
          setHasChangesMade={props.setHasChangesMade}
        />
      )}

      {/* Placeholders */}
      {props.activeSectionId === "placeholders" && (
        <Placeholders
          stylerSettings={props.stylerSettings}
          setStylerSettings={props.setStylerSettings}
          hasChangesMade={props.hasChangesMade}
          setHasChangesMade={props.setHasChangesMade}
        />
      )}

      {/* Field Descriptions */}
      {props.activeSectionId === "fieldDescriptions" && (
        <FieldDescription
          stylerSettings={props.stylerSettings}
          setStylerSettings={props.setStylerSettings}
          hasChangesMade={props.hasChangesMade}
          setHasChangesMade={props.setHasChangesMade}
        />
      )}

      {/* Text Fields */}
      {props.activeSectionId === "textFields" && (
        <TextFields
          stylerSettings={props.stylerSettings}
          setStylerSettings={props.setStylerSettings}
          hasChangesMade={props.hasChangesMade}
          setHasChangesMade={props.setHasChangesMade}
        />
      )}

      {/* Dropdown Fields */}
      {props.activeSectionId === "dropdownFields" && (
        <DropdownFields
          stylerSettings={props.stylerSettings}
          setStylerSettings={props.setStylerSettings}
          hasChangesMade={props.hasChangesMade}
          setHasChangesMade={props.setHasChangesMade}
        />
      )}

      {/* Radio Inputs */}
      {props.activeSectionId === "radioInputs" && (
        <RadioInputs
          stylerSettings={props.stylerSettings}
          setStylerSettings={props.setStylerSettings}
          hasChangesMade={props.hasChangesMade}
          setHasChangesMade={props.setHasChangesMade}
        />
      )}

      {/* Radio Inputs */}
      {props.activeSectionId === "checkboxInputs" && (
        <CheckboxInputs
          stylerSettings={props.stylerSettings}
          setStylerSettings={props.setStylerSettings}
          hasChangesMade={props.hasChangesMade}
          setHasChangesMade={props.setHasChangesMade}
        />
      )}

      {/* Paragraph Textarea */}
      {props.activeSectionId === "paragraphTextareaFields" && (
        <ParagraphTextarea
          stylerSettings={props.stylerSettings}
          setStylerSettings={props.setStylerSettings}
          hasChangesMade={props.hasChangesMade}
          setHasChangesMade={props.setHasChangesMade}
        />
      )}

      {/* Section Break */}
      {props.activeSectionId === "sectionBreakTitleDescription" && (
        <div className="stla-sidebar-settings">
          <SectionBreakTitle
            stylerSettings={props.stylerSettings}
            setStylerSettings={props.setStylerSettings}
            hasChangesMade={props.hasChangesMade}
            setHasChangesMade={props.setHasChangesMade}
          />

          <SectionBreakDescription
            stylerSettings={props.stylerSettings}
            setStylerSettings={props.setStylerSettings}
            hasChangesMade={props.hasChangesMade}
            setHasChangesMade={props.setHasChangesMade}
          />
        </div>
      )}

      {/* List Field */}
      {props.activeSectionId === "listField" && (
        <ListField
          stylerSettings={props.stylerSettings}
          setStylerSettings={props.setStylerSettings}
          hasChangesMade={props.hasChangesMade}
          setHasChangesMade={props.setHasChangesMade}
        />
      )}

      {/* Submit Button */}
      {props.activeSectionId === "submitButton" && (
        <SubmitButton
          stylerSettings={props.stylerSettings}
          setStylerSettings={props.setStylerSettings}
          hasChangesMade={props.hasChangesMade}
          setHasChangesMade={props.setHasChangesMade}
        />
      )}

      {/* Confirmation Message */}
      {props.activeSectionId === "confirmationMessage" && (
        <ConfirmationMessage
          stylerSettings={props.stylerSettings}
          setStylerSettings={props.setStylerSettings}
          hasChangesMade={props.hasChangesMade}
          setHasChangesMade={props.setHasChangesMade}
        />
      )}

      {/* Confirmation Message */}
      {props.activeSectionId === "errorMessage" && (
        <ErrorMessage
          stylerSettings={props.stylerSettings}
          setStylerSettings={props.setStylerSettings}
          hasChangesMade={props.hasChangesMade}
          setHasChangesMade={props.setHasChangesMade}
        />
      )}

      {/* General Settings */}
      {props.activeSectionId === "generalSettings" && (
        <GeneralSettings
          stylerSettings={props.stylerSettings}
          setStylerSettings={props.setStylerSettings}
          generalSettings={props.generalSettings}
          setGeneralSettings={props.setGeneralSettings}
          fetchStylerSettings={props.fetchStylerSettings}
          hasChangesMade={props.hasChangesMade}
          setHasChangesMade={props.setHasChangesMade}
        />
      )}
    </div>
  );
};

export default Sidebar;
