import { ComboboxControl } from "@wordpress/components";
import { fonts } from "../fonts";
import { useEffect } from "@wordpress/element";
import { cloneDeep } from "lodash";

const FontFamily = (props) => {
  const sectionSettings = props.panelData[props.sectionId];

  //update font family
  useEffect(() => {
    // Update the href attribute of the existing <link> element
    let link = document.getElementById("google-font-link");

    // return null when search box is cleared.. to prevent add null as font
    if (!sectionSettings.font) {
      return;
    }

    // Creating link for the first time if it doesn't exist
    if (!link) {
      // Dynamically create and append a <link> element to import the font
      link = document.createElement("link");
      link.rel = "stylesheet";
      link.id = "google-font-link";
      document.head.appendChild(link); // Append the link to the head
    }

    // Update the href attribute
    link.href = `https://fonts.googleapis.com/css2?family=${sectionSettings.font.replaceAll(
      " ",
      "+"
    )}&display=swap`;
  }, [props.panelData[props.sectionId][props.settingId]]);

  const updateFontFamily = (value) => {
    let allStylerSettings = cloneDeep(props.panelData);
    allStylerSettings[props.sectionId][props.settingId] = value;
    props.setHasChangesMade(true);
    props.setPanelData(allStylerSettings);
  };

  return (
    <ComboboxControl
      label="Font Family"
      options={fonts}
      onChange={(value) => updateFontFamily(value)}
      value={sectionSettings["font"]}
      default={{ value: "Default", label: "Default" }}
    />
  );
};

export default FontFamily;
