import { applyFilters } from "../../../Helpers/createHooks";

const Sidebar = (props) => {
  const sidebarFields = () => {
    let sidebarFields = "";
    sidebarFields = window.wp.hooks.applyFilters(
      "stla_booster_custom_themes_sidebar",
      sidebarFields,
      props.customThemesSettings,
      props.setCustomThemesSettings,
      props.formId,
      props.stylerSettings,
      props.setStylerSettings,
      props.setHasChangesMade
    );

    // console.log(props.checkboxRadioSettings);
    return sidebarFields;
  };

  return <div className="stla-panel-sidebar">{sidebarFields()}</div>;
};

export default Sidebar;
