import { useState, useEffect } from "@wordpress/element";
import { Spinner } from "@wordpress/components";
import ProPopup from "../../../Helpers/ProPopup";

import { doAction, applyFilters } from "../../../Helpers/createHooks";

const ContentArea = (props) => {
  const [html, setHtml] = useState(false);
  const [parsedHtmlWithEvents, setParsedHtmlWithEvents] = useState("");
  const [loadingApi, setLoadingApi] = useState(false);

  // return "";
  useEffect(() => {
    const controller = new AbortController();
    const signal = controller.signal;

    const fetchData = async () => {
      window.wp.hooks.doAction(
        "stla_booster_on_ai_content_load",
        // props.allSettings,
        props.formId,
        signal,
        setHtml,
        setLoadingApi,
        props.allSettings
      );
    };

    // Debouncing logic
    const debounceTimer = setTimeout(() => {
      fetchData();
    }, 500); // Adjust the debounce delay as needed

    // Cleanup function to cancel previous request when component unmounts or before next effect
    return () => {
      clearTimeout(debounceTimer);
      controller.abort();
    };
  }, [props.formId, props.formFieldsLabels]);

  //attach events when new html is set
  useEffect(() => {
    let contentAreaHtml = "";
    let popupContent = "";

    if (!props.formId) {
      // form Id not set then show form id message
      setParsedHtmlWithEvents(
        <div>Please choose the form in order to apply the styles.</div>
      );
    } else {
      // addon is active.
      if (
        window.stlaAdminGravityBooster.status[props.activeMenuBar] === "active"
      ) {
        contentAreaHtml = window.wp.hooks.applyFilters(
          "stla_booster_on_ai_html_update",
          contentAreaHtml,
          props.formId,
          html,
          props.setActiveFieldId
        );
      } else if (
        window.stlaAdminGravityBooster.status[props.activeMenuBar] ===
        "inActive"
      ) {
        popupContent = {
          title: "AI addon is not Active",
          description:
            "We're sorry, your AI addon is currently inactive. To use it you will first need to activate the addon from your plugin's menu. Click the below link to activate the addon.",
          link: `${window.stlaAdminGravityBooster.adminUrl}plugins.php`,
          primaryText: "Open Plugins",
          notice:
            "Bonus: Get 70% off on All Addons. Click Here to buy addon bundle at discounted price.",
          noticeLink: "https://wpmonks.com?p=370",
        };

        contentAreaHtml = <ProPopup content={popupContent} />;
      } else {
        // addon is not installed
        popupContent = {
          title: "AI is a Pro Addon",
          description:
            "We're sorry, the AI is premium addon. Click the below link to get the addon.",
          link: "https://wpmonks.com?p=58211",
          primaryText: "Get AI Addon",
          notice:
            "Bonus: Get 70% off on All Addons. Click Here to buy addon bundle at discounted price.",
          noticeLink: "https://wpmonks.com?p=370",
        };

        contentAreaHtml = <ProPopup content={popupContent} />;
      }
      setParsedHtmlWithEvents(contentAreaHtml);
    }
  }, [html]);

  return (
    <>
      <div className="stla-panel-contentarea stla-responsive-area">
        {window.wp.hooks.applyFilters(
          "stla_booster_ai_content_area_toolbar",
          props.formId,
          props.totalPageCount,
          props.activePage,
          props.setActivePage
        )}

        <div className="stla-form-wrapper stla-ai-form-wrapper">
          {loadingApi && (
            <div className="stla-content-spinner">
              <Spinner />
            </div>
          )}
          {loadingApi === false && parsedHtmlWithEvents}
        </div>
      </div>
    </>
  );
};

export default ContentArea;
