import { useState, useEffect } from "@wordpress/element";
import { merge, isEmpty, cloneDeep } from "lodash";
import { Spinner } from "@wordpress/components";

import Panels from "./Panels";
import Header from "./Header";
import "./index.scss";

import HelpPopup from "./HelpPopup";
import { styleProperties, generalProperties } from "./Helpers";

const App = () => {
  const [formId, setFormId] = useState(false);
  const [formFieldsLabels, setFormFieldsLabels] = useState({});
  const [stylerSettings, setStylerSettings] = useState({});
  const [generalSettings, setGeneralSettings] = useState({});
  const [tooltipsSettings, setTooltipsSettings] = useState({});
  const [materialSettings, setMaterialSettings] = useState({});
  const [bootstrapSettings, setBootstrapSettings] = useState({});
  const [checkboxRadioSettings, setCheckboxRadioSettings] = useState({});
  const [fieldIcons, setFieldIcons] = useState({});
  const [fieldIconsSettings, setFieldIconsSettings] = useState({});
  const [customThemesSettings, setCustomThemesSettings] = useState({});
  const [savingApi, setSavingApi] = useState(false);
  const [activeMenuBar, setActiveMenuBar] = useState();
  const [totalPageCount, setTotalPageCount] = useState(0);
  const [activePage, setActivePage] = useState(1);
  const [hasChangesMade, setHasChangesMade] = useState(false);
  const [isHelpPopupOpen, setIsHelpPopupOpen] = useState(false);
  const [activeSectionId, setActiveSectionId] = useState("");

  const [boosterSettings, setBoosterSettings] = useState({
    licenses: { keys: {}, status: {}, notice: { message: "", status: "" } },
    ai: {},
  });

  /**
   * Fetch Styler Styles from db and merge with default style Properties
   */
  useEffect(() => {
    if (hasChangesMade === true) {
      return;
    }

    (async () => {
      await fetchStylerSettings();
      await fetchGeneralSettings();
      await fetchFormFieldsLabels();
      await fetchBoosterSettings();
    })();

    window.wp.hooks.doAction(
      "stla_booster_load_material_settings",
      setMaterialSettings
    );

    window.wp.hooks.doAction(
      "stla_booster_load_tooltips_settings",
      setTooltipsSettings
    );

    window.wp.hooks.doAction(
      "stla_booster_load_checkbox_radio_settings",
      setCheckboxRadioSettings
    );

    window.wp.hooks.doAction(
      "stla_booster_load_bootstrap_settings",
      setBootstrapSettings
    );

    window.wp.hooks.doAction(
      "stla_booster_load_field_icons_settings",
      setFieldIcons,
      setFieldIconsSettings
    );

    window.wp.hooks.doAction(
      "stla_booster_load_custom_themes_settings",
      setCustomThemesSettings
    );
  }, [hasChangesMade]);

  useEffect(() => {
    // Fetch set the form id state
    let selectedFormId = parseInt(window.stlaAdminGravityBooster.formId);
    setFormId(selectedFormId);

    // Fetch set the panel id state
    let panelId = window.stlaAdminGravityBooster.panelId;
    setActiveMenuBar(panelId);

    // Fetch set the section id state
    let sectionId = window.stlaAdminGravityBooster.sectionId;
    setActiveSectionId(sectionId);
  }, []);

  // set total page count

  // fetch total page counts
  useEffect(() => {
    (async () => {
      const formData = new FormData();

      formData.append("action", "stla_get_page_count");
      formData.append("nonce", window.stlaAdminGravityBooster.nonce);
      formData.append("formId", window.stlaAdminGravityBooster.formId);

      try {
        await fetch(window.ajaxurl, {
          method: "POST",
          body: formData,
        })
          .then((response) => {
            if (!response.ok) {
              throw new Error("Network response was not ok");
            }
            return response.json(); // Parse response as text
          })
          .then((response) => {
            // const result = response.json();
            setTotalPageCount(response.data);
          });
      } catch (error) {
        console.error("Error fetching form HTML:", error);
      }
    })();
  }, []);

  const fetchStylerSettings = async () => {
    try {
      const formData = new FormData();
      formData.append("action", "stla_styler_settings");
      formData.append("nonce", window.stlaAdminGravityBooster.nonce);
      formData.append("formId", window.stlaAdminGravityBooster.formId);
      const response = await fetch(window.ajaxurl, {
        method: "POST",
        body: formData,
      });
      if (!response.ok) {
        throw new Error("Failed to fetch styler settings");
      }
      let mergedSettings = styleProperties;
      const result = await response.json();
      if (!!result.success) {
        mergedSettings = merge({}, styleProperties, result.data);
      }
      setStylerSettings(mergedSettings);
    } catch (error) {
      console.error("Error fetching styler settings:", error);
    }
  };

  const fetchGeneralSettings = async () => {
    try {
      const formData = new FormData();
      formData.append("action", "stla_general_settings");
      formData.append("nonce", window.stlaAdminGravityBooster.nonce);
      formData.append("formId", window.stlaAdminGravityBooster.formId);
      const response = await fetch(window.ajaxurl, {
        method: "POST",
        body: formData,
      });
      if (!response.ok) {
        throw new Error("Failed to fetch styler settings");
      }
      let mergedSettings = styleProperties;
      const result = await response.json();
      if (!!result.success) {
        mergedSettings = merge({}, generalProperties, result.data);
      }

      setGeneralSettings(mergedSettings);
    } catch (error) {
      console.error("Error fetching styler settings:", error);
    }
  };

  const fetchFormFieldsLabels = async () => {
    try {
      const formData = new FormData();
      formData.append("action", "stla_form_fields_labels");
      formData.append("nonce", window.stlaAdminGravityBooster.nonce);
      formData.append("formId", window.stlaAdminGravityBooster.formId);
      const response = await fetch(window.ajaxurl, {
        method: "POST",
        body: formData,
      });
      if (!response.ok) {
        throw new Error("Failed to fields Labels");
      }
      const result = await response.json();
      if (!!result.success) {
        setFormFieldsLabels(result.data);
      }
    } catch (error) {
      console.error("Failed to fields Labels:", error);
    }
  };

  const fetchBoosterSettings = async () => {
    try {
      const formData = new FormData();
      formData.append("action", "stla_booster_settings");
      formData.append("nonce", window.stlaAdminGravityBooster.nonce);

      const response = await fetch(window.ajaxurl, {
        method: "POST",
        body: formData,
      });
      if (!response.ok) {
        throw new Error("Failed to fetch Booster Settings");
      }
      const result = await response.json();
      if (!!result.success) {
        // console.log(result);
        let mergedSettings = merge({}, boosterSettings, result.data);
        setBoosterSettings(mergedSettings);
      }
    } catch (error) {
      console.error("Failed to fetch Booster Settings:", error);
    }
  };

  /**
   * Save Styler Settings in dAtabase
   */
  const saveStylerSettings = async () => {
    try {
      let allStyleSettings = JSON.stringify(stylerSettings);
      let allGeneralSettings = JSON.stringify(generalSettings);

      const formData = new FormData();
      formData.append("action", "stla_save_styler_settings");
      formData.append("nonce", window.stlaAdminGravityBooster.nonce);
      formData.append("formId", window.stlaAdminGravityBooster.formId);
      formData.append("stylerSettings", allStyleSettings);
      formData.append("generalSettings", allGeneralSettings);

      const response = await fetch(window.ajaxurl, {
        method: "POST",
        body: formData,
      });
      if (!response.ok) {
        throw new Error("Failed to save styler settings");
      }

      const result = await response.json();
      if (!!result.success) {
        setHasChangesMade(false);
      }
    } catch (error) {
      console.error("Error saving styler settings:", error);
    }
  };

  /**
   * Save Settings Panel Settings in database
   */
  const saveBoosterSettings = async () => {
    try {
      let allBoosterSettings = cloneDeep(boosterSettings);
      //removing licenses as they are automatically saved when activated in a different option.
      delete allBoosterSettings.licenses;
      allBoosterSettings = JSON.stringify(allBoosterSettings);

      const formData = new FormData();
      formData.append("action", "stla_save_booster_settings");
      formData.append("nonce", window.stlaAdminGravityBooster.nonce);
      formData.append("boosterSettings", allBoosterSettings);

      const response = await fetch(window.ajaxurl, {
        method: "POST",
        body: formData,
      });
      if (!response.ok) {
        throw new Error("Failed to save booster settings");
      }

      const result = await response.json();
      if (!!result.success) {
        setHasChangesMade(false);
      }
    } catch (error) {
      console.error("Error saving booster settings:", error);
    }
  };

  // gets executed on click of save button
  const saveSettings = async () => {
    if (activeMenuBar === "tooltips") {
      window.wp.hooks.doAction(
        "stla_booster_save_tooltip_settings",
        tooltipsSettings,
        formId,
        activeMenuBar,
        setSavingApi,
        setHasChangesMade
      );
    }

    if (activeMenuBar === "material") {
      window.wp.hooks.doAction(
        "stla_save_material_settings",
        materialSettings,
        formId,
        activeMenuBar,
        setSavingApi,
        setHasChangesMade
      );
    }

    if (activeMenuBar === "checkboxRadio") {
      window.wp.hooks.doAction(
        "stla_save_checkbox_radio_settings",
        checkboxRadioSettings,
        formId,
        activeMenuBar,
        setSavingApi,
        setHasChangesMade
      );
    }

    if (activeMenuBar === "bootstrap") {
      window.wp.hooks.doAction(
        "stla_save_bootstrap_settings",
        bootstrapSettings,
        formId,
        activeMenuBar,
        setSavingApi,
        setHasChangesMade
      );
    }

    if (activeMenuBar === "fieldIcons") {
      window.wp.hooks.doAction(
        "stla_save_field_icons_settings",
        fieldIcons,
        fieldIconsSettings,
        formId,
        activeMenuBar,
        setSavingApi,
        setHasChangesMade
      );
    }

    if (activeMenuBar === "settings") {
      window.wp.hooks.doAction(
        "stla_save_ai_settings",
        formFieldsLabels,
        formId,
        activeMenuBar,
        setSavingApi,
        setHasChangesMade
      );
    }

    if (activeMenuBar === "styler" || activeMenuBar === "customThemes") {
      setSavingApi(true);
      await saveStylerSettings();
      setSavingApi(false);
    }

    if (activeMenuBar === "boosterSettings") {
      setSavingApi(true);
      await saveBoosterSettings();
      setSavingApi(false);
    }
  };

  const allSettings = {
    stylerSettings,
    generalSettings,
    tooltipsSettings,
    materialSettings,
    checkboxRadioSettings,
    bootstrapSettings,
    fieldIcons,
    fieldIconsSettings,
    customThemesSettings,
    formFieldsLabels,
  };

  // wait for loading of settings of all addons before rendering further.
  for (let addonName in window.stlaAdminGravityBooster.status) {
    if (
      window.stlaAdminGravityBooster.status[addonName] === "active" &&
      window.stlaAdminGravityBooster.formId != 0
    ) {
      switch (addonName) {
        case "bootstrap":
          if (
            isEmpty(bootstrapSettings) &&
            parseInt(window.stlaAdminGravityBooster.version[addonName]) >= 2
          ) {
            return (
              <div className="stla-content-spinner">
                <Spinner />
              </div>
            );
          }
          break;
        case "checkboxRadio":
          if (
            isEmpty(checkboxRadioSettings) &&
            parseInt(window.stlaAdminGravityBooster.version[addonName]) >= 2
          ) {
            return (
              <div className="stla-content-spinner">
                <Spinner />
              </div>
            );
          }
          break;
        case "material":
          if (
            isEmpty(materialSettings) &&
            parseInt(window.stlaAdminGravityBooster.version[addonName]) >= 6
          ) {
            return (
              <div className="stla-content-spinner">
                <Spinner />
              </div>
            );
          }
          break;

        case "fieldIcons":
          if (
            isEmpty(fieldIconsSettings) &&
            parseInt(window.stlaAdminGravityBooster.version[addonName]) >= 3
          ) {
            return (
              <div className="stla-content-spinner">
                <Spinner />
              </div>
            );
          }
          break;
        case "tooltips":
          if (
            isEmpty(tooltipsSettings) &&
            parseInt(window.stlaAdminGravityBooster.version[addonName]) >= 4
          ) {
            return (
              <div className="stla-content-spinner">
                <Spinner />
              </div>
            );
          }
          break;
      }
    }
  }

  // all active addon settings have loaded.. now render
  return (
    <div className="stla-booster-wrapper">
      {/* <HelpPopup
        isHelpPopupOpen={isHelpPopupOpen}
        setIsHelpPopupOpen={setIsHelpPopupOpen}
      /> */}

      <Header
        formId={formId}
        hasChangesMade={hasChangesMade}
        savingApi={savingApi}
        setFormId={setFormId}
        saveSettings={saveSettings}
        setIsHelpPopupOpen={setIsHelpPopupOpen}
      />

      <Panels
        formId={formId}
        formFieldsLabels={formFieldsLabels}
        setFormFieldsLabels={setFormFieldsLabels}
        stylerSettings={stylerSettings}
        setStylerSettings={setStylerSettings}
        generalSettings={generalSettings}
        setGeneralSettings={setGeneralSettings}
        tooltipsSettings={tooltipsSettings}
        setTooltipsSettings={setTooltipsSettings}
        fetchStylerSettings={fetchStylerSettings}
        activeMenuBar={activeMenuBar}
        setActiveMenuBar={setActiveMenuBar}
        materialSettings={materialSettings}
        setMaterialSettings={setMaterialSettings}
        checkboxRadioSettings={checkboxRadioSettings}
        setCheckboxRadioSettings={setCheckboxRadioSettings}
        bootstrapSettings={bootstrapSettings}
        setBootstrapSettings={setBootstrapSettings}
        fieldIcons={fieldIcons}
        setFieldIcons={setFieldIcons}
        fieldIconsSettings={fieldIconsSettings}
        setFieldIconsSettings={setFieldIconsSettings}
        customThemesSettings={customThemesSettings}
        setCustomThemesSettings={setCustomThemesSettings}
        totalPageCount={totalPageCount}
        activePage={activePage}
        setActivePage={setActivePage}
        hasChangesMade={hasChangesMade}
        setHasChangesMade={setHasChangesMade}
        allSettings={allSettings}
        boosterSettings={boosterSettings}
        setBoosterSettings={setBoosterSettings}
        activeSectionId={activeSectionId}
        setActiveSectionId={setActiveSectionId}
      />
    </div>
  );
};

export default App;
