import {
  Card,
  Button,
  ColorIndicator,
  Popover,
  ColorPicker,
} from "@wordpress/components";
import { useState } from "@wordpress/element";
import { cloneDeep } from "lodash";

const Color = (props) => {
  const [isModalOpen, setIsModalOpen] = useState(false);

  const sectionSettings = props.panelData[props.sectionId];

  const modifySetting = (value) => {
    let allStylerSettings = cloneDeep(props.panelData);
    allStylerSettings[props.sectionId][props.settingId] = value;
    props.setHasChangesMade(true);
    props.setPanelData(allStylerSettings);
  };

  const backgroundStrucutre = () => {
    return (
      <>
        <label className="stla-sidebar-custom-label">{props.label}</label>
        <Card className="stla-background-component-card">
          <Button
            variant="tertiary"
            className="stla-full-width-btn"
            onClick={() => setIsModalOpen(!isModalOpen)}
          >
            <ColorIndicator colorValue={sectionSettings[props.settingId]} />
            <span style={{ paddingLeft: "10px" }}>Select Color</span>
          </Button>
        </Card>
      </>
    );
  };

  const modalStructure = () => {
    let defaultValue = "#000";
    if (sectionSettings[props.settingId] !== "") {
      defaultValue = sectionSettings[props.settingId];
    }
    if (isModalOpen) {
      return (
        <Popover
          onFocusOutside={() => setIsModalOpen(false)}
          placement="right-start"
          className="stla-color-popover"
        >
          <ColorPicker
            color={sectionSettings[props.settingId]}
            onChange={modifySetting}
            defaultValue={defaultValue}
          />
          <Button varient="link" onClick={() => modifySetting("")}>
            Reset
          </Button>
        </Popover>
      );
    }

    return "";
  };

  return (
    <>
      {backgroundStrucutre()}
      {modalStructure()}
    </>
  );
};

export default Color;
