import { cloneDeep } from "lodash";
import { __experimentalBoxControl as BoxControl } from "@wordpress/components";

const Spacing = (props) => {
  const sectionSettings = props.panelData[props.sectionId];

  const updateSpacing = (value) => {
    let allStylerSettings = cloneDeep(props.panelData);
    allStylerSettings[props.sectionId][`${props.settingId}-left`] = value.left;
    allStylerSettings[props.sectionId][`${props.settingId}-right`] =
      value.right;
    allStylerSettings[props.sectionId][`${props.settingId}-top`] = value.top;
    allStylerSettings[props.sectionId][`${props.settingId}-bottom`] =
      value.bottom;
    props.setHasChangesMade(true);
    props.setPanelData(allStylerSettings);
  };

  return (
    <BoxControl
      label={props.label}
      values={{
        left: sectionSettings[`${props.settingId}-left`],
        right: sectionSettings[`${props.settingId}-right`],
        top: sectionSettings[`${props.settingId}-top`],
        bottom: sectionSettings[`${props.settingId}-bottom`],
      }}
      onChange={(value) => updateSpacing(value)}
    />
  );
};

export default Spacing;
