import Sidebar from "./Sidebar";
import ContentArea from "./ContentArea";
import { useEffect, useState } from "@wordpress/element";

const Settings = (props) => {
  useEffect(() => {
    // Fetch set the panel id state
    let sectionId = window.stlaAdminGravityBooster.sectionId;
    if (sectionId !== "") {
      props.setActiveSectionId(sectionId);
    } else {
      props.setActiveSectionId("licenses");
    }
  }, []);

  return (
    <div className="stla-panel">
      <Sidebar
        activeSectionId={props.activeSectionId}
        setActiveSectionId={props.setActiveSectionId}
        formId={props.formId}
      />
      <ContentArea
        activeMenuBar={props.activeMenuBar}
        activeSectionId={props.activeSectionId}
        setActiveSectionId={props.setActiveSectionId}
        formId={props.formId}
        boosterSettings={props.boosterSettings}
        setBoosterSettings={props.setBoosterSettings}
        setHasChangesMade={props.setHasChangesMade}
      />
    </div>
  );
};

export default Settings;
